.filtering {
    display: flex;
    justify-content: flex-start;
}
.filtering input {
    width: 80%;
    border-radius: 0;
}

.filtering p {
    display: inline-block;
    margin: 8px;
}

@media only screen  and (max-width: 1130px) {
    .filtering {
        display: block;
        width: 100%;

    }


}